:root {
  --PINK: #e86f54;
  --DARK-PINK: #e25b41;
  --WHITE: #fff;
  --BLACK: #000;
  --LIGHTGREY: #5c5c5c;
  --INACTIVE_BROWN: #e86f5466;
  --FADEDBLACK: #111111b3;
  --CORRECTCOLOR: #3cbc81;
  --ERRORCOLOR: #e74a3b;
}

body {
  background: var(--WHITE);
  color: var(--BLACK);
  font-family: Montserrat, sans-serif;
}

ul {
  padding-left: 0;
  list-style: none;
}

img {
  display: block;
}

*, :after, :before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.mobile-menu {
  flex-flow: column;
  display: flex;
}

.mobile-menu-close {
  justify-content: flex-end;
  width: 100%;
  display: flex;
}

.js-menu-container {
  background-color: var(--background-color);
  z-index: 999;
  width: 100vw;
  height: 100vh;
  padding: 32px;
  transition: transform .25s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.js-menu-container.is-open {
  transform: translateX(0);
}

.mobile-menu-close .menu-toggle {
  position: absolute;
  top: 16px;
  right: 16px;
}

