.delivery-section h2 {
  color: var(--BLACK);
  margin: 0 0 32px;
  font-family: Montserrat;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  position: relative;
}

.delivery-section h2:after {
  content: "";
  background-color: var(--BLACK);
  width: 148px;
  height: 1px;
  margin-left: 14px;
  position: absolute;
  top: 50%;
}

.delivery-list {
  counter-reset: item;
  margin: 0;
  list-style-type: none;
}

.delivery-list li {
  justify-content: space-around;
  display: flex;
  position: relative;
}

.delivery-list li span {
  color: var(--BLACK);
  margin: 0 0 14px 8px;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.delivery-list li:before {
  content: counter(item) "";
  counter-increment: item;
  color: var(--BLACK);
  background-color: #0000;
  border: 1px solid #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  font-family: Montserrat;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  display: flex;
  position: absolute;
  left: -2em;
}

.delivery-img {
  width: 100%;
  margin: 26px 0 0;
}

.delivery-list li:hover:before {
  background-color: coral;
}

.backdrop {
  opacity: 1;
  z-index: 30;
  background-color: #0a0a0a80;
  width: 100%;
  height: 100%;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.modal {
  background: var(--WHITE);
  width: 272px;
  padding: 28px 16px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.close_modal_button {
  background-color: #0000;
  border: none;
  width: 18px;
  height: 18px;
  padding: 0;
  transition: transform .25s;
  position: absolute;
  top: 16px;
  right: 16px;
}

.close_modal_button:hover {
  transform: scale(1.05);
}

.contact_us_modal_wraper {
  flex-direction: column;
  display: flex;
}

.contact_us_modal_head {
  font-size: 20px;
  font-weight: 600;
}

.contact_us_modal_txt {
  color: var(--LIGHTGREY);
  margin: 14px 0 18px;
  font-size: 12px;
  font-weight: 500;
}

.contact_us_modal_form_wrapper label:first-child {
  margin-bottom: 14px;
}

.contact_us_modal_form_wrapper label:nth-child(2) {
  margin-bottom: 24px;
}

.contact_us_userdata_modal_label {
  color: var(--LIGHTGREY);
  flex-direction: column;
  justify-content: space-between;
  gap: 8px;
  font-size: 10px;
  font-weight: 500;
  display: flex;
}

.valid {
  color: var(--CORRECTCOLOR);
}

.invalid {
  color: var(--ERRORCOLOR);
}

.valid_name_modal_icon, .invalid_name_modal_icon {
  display: none;
  position: absolute;
  top: 48%;
  left: 86%;
}

.valid_email_modal_icon, .invalid_email_modal_icon {
  display: none;
  position: absolute;
  top: 67%;
  left: 86%;
}

.contact_us_name_modal_input:valid + .valid_name_modal_icon {
  fill: var(--CORRECTCOLOR);
  display: inline-block;
}

.invalid_name_modal_icon.wrong {
  fill: var(--ERRORCOLOR);
  display: inline-block;
}

.contact_us_email_modal_input:valid + .valid_email_modal_icon {
  fill: var(--CORRECTCOLOR);
  display: inline-block;
}

.invalid_email_modal_icon.wrong {
  fill: var(--ERRORCOLOR);
  display: inline-block;
}

.contact_us_email_modal_input:not(:placeholder-shown):required:valid, .contact_us_name_modal_input:not(:placeholder-shown):required:valid {
  border-color: var(--CORRECTCOLOR);
  transform: scale(1.005);
}

.contact_us_email_modal_input:not(:placeholder-shown):required:invalid, .contact_us_name_modal_input:not(:placeholder-shown):required:invalid {
  border-color: var(--ERRORCOLOR);
  transform: scale(1.005);
}

.contact_us_name_modal_input {
  border: 1px solid #d7d7d7;
  border-radius: 4px;
  align-items: center;
  width: 240px;
  height: 35px;
  padding: 10px 24px 10px 12px;
  font-family: Montserrat;
  font-size: 12px;
  font-weight: 500;
  transition: transform .25s;
  display: flex;
}

.contact_us_email_modal_input:hover, .contact_us_name_modal_input:hover {
  border: 1px solid var(--FADEDBLACK);
  transform: scale(1.005);
}

.contact_us_email_modal_input:focus, .contact_us_name_modal_input:focus {
  outline: none;
}

.contact_us_email_modal_input {
  border: 1px solid #d7d7d7;
  border-radius: 4px;
  align-items: center;
  width: 240px;
  height: 35px;
  padding: 10px 24px 10px 12px;
  font-family: Montserrat;
  font-size: 12px;
  font-weight: 500;
  transition: transform .25s;
  display: flex;
}

.contact_us_modal_button {
  cursor: not-allowed;
  color: var(--WHITE);
  background: var(--INACTIVE_BROWN);
  border: none;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 240px;
  padding: 10px 0;
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 600;
  display: flex;
}

.contact_us_modal_button.valid {
  background: var(--PINK);
  cursor: pointer;
  transition: transform .25s;
}

.contact_us_modal_button.valid:hover {
  background: var(--DARK-PINK);
  transform: scale(1.005);
}

